@import "./variables";

@mixin mobile {
    @media (max-width: #{$breakpoint-mobile}) {
        @content;
    }
}
@mixin notebook {
    @media (max-width: #{$breakpoint-notebook}) {
        @content;
    }
}
@mixin desktop {
    @media (max-width: #{$breakpoint-desktop}) {
        @content;
    }
}